import { Injectable } from '@angular/core';
import { HttpResponseModel } from '@models/http/response.model';
import { UserModel } from '@models/user.model';
import { map } from 'rxjs/operators';
import { ApiService } from '@services/api.service';
import { ConfigService } from './config.service';
import { Observable } from 'rxjs';
import { VisitModel } from '@models/visit.model';

@Injectable({
  providedIn: 'root'
})
export class VisitService {
  getVisitsOfUser(user: number, status: number, date: Date): Observable<VisitModel[]> {
    let d = date.getDate();
    let m = date.getMonth() + 1;
    let y = date.getFullYear();
    return this.$api.get(`/visit/user/${user}/status/${status}/date/${d}/${m}/${y}`).pipe(
      map((result: HttpResponseModel): VisitModel[] => result.data.visits));
  }

  getHostsOfVisit(id: number): Observable<HttpResponseModel> {
    return this.$api.get(`/visit/${id}/host/all`);
  }

  getVisitsOfVisitor(user: number, status: number, date: Date): Observable<VisitModel[]> {
    let d = date.getDate();
    let m = date.getMonth() + 1;
    let y = date.getFullYear();
    return this.$api.get(`/visit/visitor/${user}/status/${status}/date/${d}/${m}/${y}`).pipe(
      map((result: HttpResponseModel): VisitModel[] => result.data.visits));
  }

  getVisitsOfDepartment(department: number): Observable<VisitModel[]> {
    return this.$api.get(`/visit/department/${department}/all`).pipe(
      map((result: HttpResponseModel): VisitModel[] => result.data.visits));
  }

  constructor(
    private $api: ApiService,
    public config: ConfigService,
  ) {
  }
}
